<template>
  <v-form class="pl-3">
    <v-row>
      <v-col class="pb-0">
        <v-text-field
          dark
          class="mt-0 mr-auto"
          placeholder="Enter New Checklist Title"
          v-model="checklist.title"
        />
      </v-col>
      <v-col cols="2" class="pl-0 pt-5">
        <v-menu class="primray lighten-2">
          <template v-slot:activator="{ on }">
            <v-icon
              dense
              v-on="on"
              dark
              :disabled="!checklist.title"
              class="pt-2 float-right"
              >mdi-dots-vertical</v-icon
            >
          </template>
          <v-list dark color="#1565C0">
            <v-list-item @click="clearForm">
              <v-list-item-title dark>Clear the Form</v-list-item-title>
            </v-list-item>
            <v-list-item @click="openOptions = true" v-if="!openOptions">
              <v-list-item-title dark>Open Options</v-list-item-title>
            </v-list-item>
            <v-list-item @click="openOptions = false" v-if="openOptions">
              <v-list-item-title dark>Close Options</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
        <v-btn
          @click="saveHandler"
          text
          dark
          :disabled="!checklist.title"
          min-width="24"
          class="pl-0 pr-0 float-right"
        >
          <v-icon dense dark>mdi-content-save</v-icon>
        </v-btn>
      </v-col>
    </v-row>
    <v-row v-if="checklist.title && !checklist.sourceMasterId && openOptions">
      <v-col cols="12" class="pt-0 pb-0">
        <v-checkbox
          dark
          class="mt-0"
          v-model="checklist.masterChecklist"
          label="Master Checklist"
        />
      </v-col>
    </v-row>
    <v-row v-if="checklist.title && openOptions">
      <v-col cols="11" class="mt-2 py-0">
        <v-select
          dark
          class="pt-0"
          label="Folder"
          v-model="checklist.folderName"
          :items="folderOptions"
        />
      </v-col>
    </v-row>
    <v-row v-if="checklist.title && !checklist.sourceMasterId">
      <v-col class="pb-0">
        <v-textarea
          rows="1"
          auto-grow
          dark
          class="pt-0"
          v-model="newItemSubject"
          label="Enter New Item"
          @change="addItem"
          @keydown.enter="addItem"
          append-outer-icon="mdi-plus"
          @click:append-outer="addItem"
        />
      </v-col>
    </v-row>
    <v-row>
      <v-col class="pt-0">
        <draggable
          :list="checklist.items"
          ghost-class="ghost"
          handle=".v-input__append-outer"
          @start="dragging = true"
          @end="dragging = false"
        >
          <ChecklistItem
            v-for="item in checklist.items"
            :key="item.key"
            :item="item"
          />
        </draggable>
      </v-col>
    </v-row>
  </v-form>
</template>

<script>
// const uuidv4 = require('uuid/v4');
import uuidv4 from 'uuid/v4'
import { mapActions, mapGetters } from 'vuex'
import draggable from 'vuedraggable'
import ChecklistItem from '@/components/ChecklistItem'

export default {
  name: 'Checklist',
  components: {
    ChecklistItem,
    draggable
  },
  computed: {
    checklist() {
      return this.$store.state.checklist.selectedChecklist
    },
    folderOptions() {
      return ['<ROOT>', ...this.userFolders]
    },
    ...mapGetters({ userFolders: 'auth/userFolders', ownerId: 'auth/ownerId' })
  },
  data() {
    return {
      newItemSubject: '',
      dragging: false,
      openOptions: false
    }
  },
  methods: {
    addItem() {
      // this.newItemSubject = this.newItemSubject.trimEnd()
      let subject = this.newItemSubject.trimEnd()
      let cleanSubject
      if (subject.charAt(subject.length - 1) === String.fromCharCode(10)) {
        cleanSubject = subject.substring(0, subject.length - 1)
      } else {
        cleanSubject = subject
      }
      if (cleanSubject) {
        if (!this.checklist.items) this.checklist.items = []
        this.checklist.items.push({
          key: uuidv4(),
          subject: cleanSubject,
          completed: false
        })
      }
      this.newItemSubject = ''
    },
    saveHandler() {
      if (this.checklist.title) {
        const newChecklist = { ...this.checklist }
        if (!this.checklist.ownerId) newChecklist.ownerId = this.ownerId
        if (this.checklist.folderName === '<ROOT>') {
          newChecklist.folderName = ''
        }
        this.save(this.checklist)
      }
    },
    ...mapActions({
      save: 'checklist/save',
      clearForm: 'checklist/clearForm'
    })
  }
}
</script>

<style>
.v-input__slot {
  margin-bottom: 0 !important;
}
.v-select-list .v-list-item:hover {
  background-color: #eee;
}
</style>
